.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.8rem 5rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header h1{
    text-transform: uppercase;
}
.header-bg{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}
.nav-menu{
    display: flex;
}
.nav-menu li{
    padding: 0 2.2rem;
}
.nav-menu li a{
    font-size: 1.3rem;
    font-weight: 500;
}
.nav-menu li a:hover{
    color: rgb(248, 217, 15);
}
.hamburger{
    display: none;
}




@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.6s;
    }
    .nav-menu.active{
        left: 0;

    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }
    
    
}