@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700;600;500;400;300;200;100&display=swap');

*{
  font-family: 'Montserrat', sans-serif;
  margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: none;
	border: none;
	text-decoration: none;

}
body{
  background: #000;
}
h1, h4, p,
a{
  color: #fff;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(248, 217, 15);
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
  border-radius: .5rem;
}
.btn-light{
  /* background: rgba(255, 255, 255, 0.2); */
  background: transparent;
  color: #fff;
}
.btn:hover{
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: 0.3s;
}